<template>
    <div class="box">
        <HeaderCom :backShow="backShow"/>
        <div class="wechat">
            <img :src="src" alt="">
            <h3>,(*)</h3>
        </div>
        <div class="map-intro">
            <h4>:<span>()</span></h4>
        </div>
        <div id="allmap"></div>
        <div class=""></div>
        <NavCom></NavCom>
        <Totop/>
    </div>
</template>

<script>
import NavCom from "@/components/NavCom";
import HeaderCom from "@/components/HeaderCom";
import Totop from "@/components/Totop";
export default {
    name: 'About',
    data() {
        return {
            title:"",
            backShow:false,
            src: './static/images/qrcode.jpg'
        }
    },
    components :{
    NavCom,
    HeaderCom,
    Totop,
  },
  mounted(){
      // API
	var map = new BMap.Map("allmap");    // Map
	map.centerAndZoom(new BMap.Point(118.19, 24.49), 5);  // ,
	// 
	map.addControl(new BMap.MapTypeControl({
		mapTypes:[
            BMAP_NORMAL_MAP,
            BMAP_HYBRID_MAP
        ]}));	  
	map.setCurrentCity("");          //  
    map.enableScrollWheelZoom(true);     //
    // map.addEventListener("click", function(e){    
    //             var lat = e.point.lat;
    //             var lng = e.point.lng;
    //             alert(''+lng+ ''+lat);
	//         });
        var myIcon = new BMap.Icon("./static/images/posi.png", new BMap.Size(32, 32), {
            // 
            offset: new BMap.Size(10, 25),
            //    
            imageOffset: new BMap.Size(0, 0) //   
        });
        var point = new BMap.Point(118.189,24.491);
        //  
        var marker = new BMap.Marker(point,{icon: myIcon});
        map.addOverlay(marker);
  }
}

</script>
<style scoped>
    .box{
        background: #f2f2f2;
        background: url(../../static/images/bg.jpeg);
        background-size: contain;
    }
    #allmap{
        width: 90%;
        margin: 0 auto;
        height: 200px;
        border: 1px solid #ccc;
        box-shadow: 0px 0px 20px #000;
        border-radius: 5px;
    }
    .wechat img{
        display: block;
        margin: 0 auto;
        border-radius: 50%;
        width: 40%;
        height: auto;
        box-shadow: 1px 1px 20px #000;
    }
    .map-intro span{
        color: red;
        font-size: 12px;
        font-weight: 300;
    }
</style>



